// Global SCSS imports
@import "vars";
@import "reset";
@import "global";
@import "fonts";
@import "mixin";
@import "extend";

// Sections SCSS files imports

.header{
  height: 100vh;
  width: 100%;
  &__inner{
    display: flex;
  }
  
  &__img{
    overflow: hidden;
    height: 100vh;
    img{
      height: 100%;
    }
  }

  &__content{
    padding: 68px 20px 61px 100px;
    display: flex;
    flex-direction: column;
  }

  &__title{
    margin-top: 90px;;
  }

  &__text{
    margin-bottom: 40px;
  }

  &__btn{
    padding:18px 30px;
    max-width: 250px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:hover{
      svg{
        transition: all .3s;
        transform: translateX(10px)
    }
    }
  }

  &__bottom{
    display: flex;
    margin-top: auto;

    &-text{
    font-weight: 375;
    font-size: 18px;
    line-height: 20px;
    color: $fsgray;
    }
    &-link{
      margin-left: 5px;
      font-weight: 494;
      font-size: 20px;
      line-height: 22px;
      color: $fsred;
      transition: all .3s;
      &:hover{
        color: $Hovbg;
      }
    }
  }
}

//---------------------------HUB---------

.steps__numbers{
  display: flex;
  align-items: center;
  position: relative;
}

.progress{
  position: absolute;
  max-width: 650px;
  top: 50%;
  right: 0;
  left: 0;
  height: 2px;
  background-color: #E5E4E1;
  z-index: -1;
  &__success{

  }
}

.step__number{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border: 1px solid #B3B4BA;
  border-radius: 50%;
  background-color: #fff;
  & + .step__number{
    margin-left: 100px;
  }
}

.steps{
  &__title{
    margin-top: 60px;
  }
  &__text{
    margin-bottom: 40px;
  }
}

.form__input{
  max-width: 460px;
  position: relative;
  &-search{
    width: 100%;
    padding: 20px 65px;
    font-weight: 375;
    font-size: 18px;
    line-height: 20px;
    max-width: 460px;
  }

  svg{
    position: absolute;
    top: 30%;
    left: 30px;
  }
}

.radio__button{
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 20px;
  &-label{
    max-width: 360px;
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: $Bgblue;
    position: relative;
  }
  
  &-input{
    display: none;
    &:checked + .radio__label-face{
      border: 1px solid $Bgred;
      &::after{
      opacity: 1;
    }
    }
  }
}
  
  .radio__label-img{
    margin-right: 30px;
    padding: 10px;
    background-color: #fff;
  }

  .radio__label-face{
    width: 30px;
    height: 30px;
    border: 1px solid $Bggray;
    background-color: #fff;
    border-radius: 50%;
    margin-left: auto;
    position: absolute;
    right: 30px;
    &::after{
      content: "";
      position: absolute;
      display: block;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      top: 50%;
      left: 50%;
      background-color: $Bgred;
      transform: translate(-50%,-50%);
      opacity: 0;
    }
  }


